export default "import * as PIXI from \"pixi.js\";\nimport { Application, Text } from \"@danneu/solid-pixi\";\nimport { Suspense, type Component } from \"solid-js\";\n\nconst BasicText: Component = () => {\n  return <Text text=\"Basic text in pixi\" x={50} y={100} />;\n};\n\nconst RichText: Component = () => {\n  const linearGradient = new PIXI.FillGradient({\n    type: \"linear\",\n    start: { x: 0, y: 0 }, // Start at top\n    end: { x: 0, y: 10 }, // End at bottom\n    colorStops: [\n      { offset: 0, color: 0xffffff },\n      { offset: 0.5, color: 0x00ff99 },\n    ],\n    // Use normalized coordinate system where (0,0) is the top-left and (1,1) is the bottom-right of the shape\n    textureSpace: \"local\",\n  });\n\n  const style = new PIXI.TextStyle({\n    fontFamily: \"Arial\",\n    fontSize: 36,\n    fontStyle: \"italic\",\n    fontWeight: \"bold\",\n    fill: linearGradient,\n    stroke: { color: \"#4a1850\", width: 5, join: \"round\" },\n    dropShadow: {\n      color: \"#000000\",\n      blur: 4,\n      angle: Math.PI / 6,\n      distance: 6,\n    },\n    wordWrap: true,\n    wordWrapWidth: 440,\n  });\n\n  return (\n    <Text\n      x={50}\n      y={220}\n      style={style}\n      text=\"Rich text with a lot of options and across multiple lines\"\n    />\n  );\n};\n\nconst SkewText: Component = () => {\n  const style = new PIXI.TextStyle({\n    fontFamily: \"Arial\",\n    dropShadow: {\n      alpha: 0.8,\n      angle: 2.1,\n      blur: 4,\n      color: \"0x111111\",\n      distance: 10,\n    },\n    fill: \"#ffffff\",\n    stroke: { color: \"#004620\", width: 12, join: \"round\" },\n    fontSize: 60,\n    fontWeight: \"lighter\",\n  });\n\n  return (\n    <Text\n      text=\"SKEW IS COOL\"\n      anchor={0.5}\n      x={300}\n      y={480}\n      style={style}\n      skew={{ x: 0.65, y: -0.3 }}\n    />\n  );\n};\n\nexport default function () {\n  return (\n    <>\n      <Suspense fallback={<div>Loading</div>}>\n        <Application backgroundColor={0x1099bb}>\n          <BasicText />\n          <RichText />\n          <SkewText />\n        </Application>\n      </Suspense>\n    </>\n  );\n}\n"