export default "import { Application, Graphics } from \"@danneu/solid-pixi\";\nimport { Suspense, type Component } from \"solid-js\";\n\nconst Example: Component = () => {\n  return (\n    <Graphics\n      ref={(g) => {\n        // Rectangle\n        g.rect(50, 50, 100, 100);\n        g.fill(0xde3249);\n\n        // Rectangle + line style 1\n        g.rect(200, 50, 100, 100);\n        g.fill(0x650a5a);\n        g.stroke({ width: 2, color: 0xfeeb77 });\n\n        // Rectangle + line style 2\n        g.rect(350, 50, 100, 100);\n        g.fill(0xc34288);\n        g.stroke({ width: 10, color: 0xffbd01 });\n\n        // Rectangle 2\n        g.rect(530, 50, 140, 100);\n        g.fill(0xaa4f08);\n        g.stroke({ width: 2, color: 0xffffff });\n\n        // Circle\n        g.circle(100, 250, 50);\n        g.fill(0xde3249, 1);\n\n        // Circle + line style 1\n        g.circle(250, 250, 50);\n        g.fill(0x650a5a, 1);\n        g.stroke({ width: 2, color: 0xfeeb77 });\n\n        // Circle + line style 2\n        g.circle(400, 250, 50);\n        g.fill(0xc34288, 1);\n        g.stroke({ width: 10, color: 0xffbd01 });\n\n        // Ellipse + line style 2\n        g.ellipse(600, 250, 80, 50);\n        g.fill(0xaa4f08, 1);\n        g.stroke({ width: 2, color: 0xffffff });\n\n        // Draw a shape\n        g.moveTo(50, 350);\n        g.lineTo(250, 350);\n        g.lineTo(100, 400);\n        g.lineTo(50, 350);\n        g.fill(0xff3300);\n        g.stroke({ width: 4, color: 0xffd900 });\n\n        // Draw a rounded rectangle\n        g.roundRect(50, 440, 100, 100, 16);\n        g.fill(0x650a5a, 0.25);\n        g.stroke({ width: 2, color: 0xff00ff });\n\n        // Draw star\n        g.star(360, 370, 5, 50);\n        g.fill(0x35cc5a);\n        g.stroke({ width: 2, color: 0xffffff });\n\n        // Draw star 2\n        g.star(280, 510, 7, 50);\n        g.fill(0xffcc5a);\n        g.stroke({ width: 2, color: 0xfffffd });\n\n        // Draw star 3\n        g.star(470, 450, 4, 50);\n        g.fill(0x55335a);\n        g.stroke({ width: 4, color: 0xffffff });\n\n        // Draw polygon\n        const path = [600, 370, 700, 460, 780, 420, 730, 570, 590, 520];\n\n        g.poly(path);\n        g.fill(0x3500fa);\n      }}\n    />\n  );\n};\n\nexport default function () {\n  return (\n    <Suspense fallback={<div>Loading</div>}>\n      <Application antialias>\n        <Example />\n      </Application>\n    </Suspense>\n  );\n}\n"